 } from 'vuex-class'

import {OrbitFS} from '../agent/fs';

@Component
export default class Petition extends Vue {
   signed = false;
   @Getter key;

   get petitions() {
     return this.$store.state.petition.petitions;
   }

   created() {
     this.$store.dispatch('petition/fetchState', this.key);
   }

   sign(petition: any) {
     petition.signed = !petition.signed;
   }

}



